<template>
  <div class="card">
    <page-title :option="title"></page-title>
    <div class="card-body">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="Active NFT" name="active">
          <dynamic-table :data="tableData" :header="tableHeader" :operations="operations" @commands="view"></dynamic-table>
          <el-pagination background layout="prev, pager, next" :total="total" v-model:current-page="pageNum" @current-change="handlePageChange" :page-size="pageSize" />
        </el-tab-pane>
        <el-tab-pane label="Used NFT" name="used">
          <dynamic-table :data="tableData" :header="tableHeader1" :operations="operations" @commands="view"></dynamic-table>
          <el-pagination background layout="prev, pager, next" :total="total" v-model:current-page="pageNum" @current-change="handlePageChange" :page-size="pageSize" />
        </el-tab-pane>
        <el-tab-pane label="BUY" name="buy">Buy</el-tab-pane>
      </el-tabs>

      <template v-if="activeName =='buy'">
      </template>
    </div>
    <!--View NFT on Blockchain-->
    <el-dialog v-model="visible" title="View NFT on Blockchain" width="800px">
      <div class="card ">
        <div class="card-header card-header-info card-header-icon">
          <div class="card-icon">
            <i class="fa fa-ticket"></i>
          </div>
          <h4 class="card-title">View NFT on Blockchain</h4>
        </div>
        <div class="card-body ">
          <div class="row">
            <div class="col-md-4">
              <el-image :src="rowData.src" style="width:200px"></el-image>
            </div>
            <div class="col-md-8 ml-auto mr-auto">
              <div class="table-responsive table-sales">
                <table class="table">
                  <tbody>
                    <template v-for="(item,key) in rowData">
                      <tr v-if="key !='src'" :key="key">
                        <td>{{ key.replace(/\_/g," ")+":" }}</td>
                        <td>{{ item }}</td>
                      </tr>
                    </template>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </el-dialog>
    <!--View NFT on Blockchain-->
  </div>
</template>
<script setup>
import { ref, onMounted } from "vue";
import { nftApi } from "@/api/request";
import { chainApi } from "@/api/request";
import PageTitle from "@/components/page-title.vue";
import DynamicTable from "@/components/dynamic-table.vue";
let activeName = ref("active");
let title = ref({ type: "warning", title: "NFTs", desc: "All NFTs for points competitions which come from blind boxes" })
let tableData = ref([])
let tableHeader = ref(["id", "nft_type", "blockchain", "minted_at", "game_chances"])
let tableHeader1 = ref(["id", "nft_type", "blockchain", "minted_at", "run_out_time"])
let operations = ref([{ id: 1, type: 'success',icon:"fa fa-external-link", name: "view", event: 'view' }])
let visible = ref(false);
let rowData = ref({});
let pageNum = ref(1); let total = ref(1);
let pageSize = ref(10);
function view(data) {
  if (data.command == "view") {
    visible.value = true;
    queryRow(data.data);
  }
}
function query() {
  let data = {
    pageSize: pageSize.value,
    pageNum: pageNum.value,
    type: activeName.value
  }
  nftApi.list(data).then((res) => {
    if (res.data.data) {
      tableData.value = res.data.data;
      total.value = res.data.total;
    }
  })
}
function queryRow(data) {
  let res = [data].map(i => {
    return {
      NFT_type: i.nft_type,
      Game_chances: i.game_chances,
      Blockchain: i.blockchain,
      Token_ID: i.token_id,
      Contract_Address: i.contract_address,
      Minted_at: i.minted_at,
      src: i.src
    }
  });
  rowData.value = res[0];
}
function handleClick(tab) {
  activeName.value = tab.paneName;
  query();
}
function handlePageChange(val) {
  pageNum.value = val;
  query();
}
onMounted(() => {
  query();
})
</script>